import { Grid, Paper, Typography, Button, Box } from "@material-ui/core";
import PutInputs from "../../Inputs";
import { generalSettingsinputs } from "../../../inputdb/generalSettings";
import useStyles from "../../../styling/inputstyle";
import { useState } from "react";
import user from "../../../imgs/user.svg";
import { useForm } from "react-hooks-helper";
export const GeneralSettings = (props) => {
  const classes = useStyles();
  const data = JSON.parse(localStorage.getItem("user"))
    ? JSON.parse(localStorage.getItem("user"))
    : props.data;
  const defaultValues = {
    FirstName: data.firstname,
    LastName: data.lastname,
    password: "",
    email: data.email,
    role: "",
  };

  const [{ FirstName, LastName, password, email, role }, setValue] =
    useForm(defaultValues);
  const [pictures, setPic] = useState({});
  const [prevpic, setPPic] = useState("");
  const [Salert, setSAlert] = useState({ state: false, msg: "" });
  const [Walert, setWAlert] = useState({ state: false, msg: "" });
  const [Phone, setPhone] = useState(data.phone);
  const onDrop = (picture, file) => {
    setPic(picture[0]);
    setPPic(file);
  };
  return (
    <>
      <Grid container spacing={3}  style={{paddingBottom: "2rem"}}>
        <Grid item xs={12} md={6}>
          <Paper className={classes.rpaper}>
            <Typography className={classes.userhead}>User Logo :</Typography>
            <Box
              display="flex"
              justifyContent="center"
              flexDirection="column"
              alignItems="center"
            >
              <img
                className={classes.profilepic}
                src={prevpic === "" ? user : prevpic}
              />
              <PutInputs
                setPic={onDrop}
                icon="none"
                inputs={[
                  {
                    id: 17,
                    name: "uplogo",
                    labem: false,
                  },
                ]}
                btn="none"
              />
            </Box>
          </Paper>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.rpaper}>
            <Typography className={classes.userhead}>
              User information :
            </Typography>
            <PutInputs
              FirstName={FirstName}
              LastName={LastName}
              email={email}
              password={password}
              setPhone={setPhone}
              setPic={onDrop}
              setValue={setValue}
              role={role}
              Phone={Phone}
              inputs={generalSettingsinputs}
              form="none"
              btn="none"
            />
            <Box display="flex" justifyContent="flex-end">
              <Button
                variant="contained"
                color="secondary"
                style={{
                  borderColor: "rgb(76, 175, 80)",
                  color: "white",
                  backgroundColor: "rgb(76, 175, 80)",
                  marginBottom: "16px",
                }}
                //   onClick={() => {
                //     createuser();
                //   }}
              >
                Submit
              </Button>
            </Box>
          </Paper>
        </Grid>
      </Grid>
    </>
  );
};

export const ChangePassword = () => {
  return "changePassw";
};
