import { Box, Container, Grid, Paper } from "@material-ui/core";
import { useStyles } from "./contactStyle";
import { LeftSide } from "./LeftSide";
import { RightSide } from "./RightSide";
import { useEffect } from "react";
import "aos/dist/aos.css";
import AOS from "aos";
import { useHistory } from "react-router-dom";
import mailman from "../../../imgs/mailman.png";
import { Getintouch } from "../Getintouch";
export const Contactus = (props) => {
  let history = useHistory();
  const classes = useStyles();
  const redirect = () => {
    if (props.token) history.push("/account");
  };
  useEffect(() => {
    AOS.init({ once: false });
    redirect();
    window.scroll(0, 0);
  }, []);
  return (
    <Container className={classes.root} maxWidth="xl">
      <Container  maxWidth="lg">
        <Grid container alignItems="center">
          <Grid item xs={12} lg={6} data-aos="fade-right" data-aos-once="true">
            <Getintouch />
          </Grid>
          <Grid item xs={12} lg={6} data-aos="fade-left" data-aos-once="true">
            <img src={mailman} style={{ width: "100%" }} />
          </Grid>
        </Grid>
      </Container>
      <Paper elevation={3} className={classes.pap}>
        <Grid container justify="space-between" align="center">
          <LeftSide />
          <RightSide />
        </Grid>
      </Paper>
    </Container>
  );
};
