import { Grid } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import { useStyles } from "./contactStyle";
import { withStyles } from "@material-ui/core/styles";
import round from "../../../imgs/circle3.svg";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";

import { MuiThemeProvider, createTheme } from "@material-ui/core/styles";

const theme = createTheme({
  palette: {
    secondary: {
      main: "#f44336",
    },
  },
});

const inputs = [
  {
    label: "First Name",
    width: "90",
    helper: "",
  },
  {
    label: "Last Name",
    helper: "",
  },
  {
    label: "Phone Number",
    width: "90",
    helper: "+212600000000",
  },
  {
    label: "Mail",
    helper: "exemple@mail.com",
  },
  {
    label: "Message",
    placeholder: "Write your Message",
    type: "multiline",
    helper: "50 - 100 Words",
    row: 5,
  },
];

const ColorButton = withStyles((theme) => ({
  root: {
    color: "white",
  },
}))(Button);

export const RightSide = () => {
  const classes = useStyles();
  return (
    <Grid container item xs={12} md={8} className={classes.col2} align="left">
      <img src={round} className={classes.circle3} alt="img" />
      <MuiThemeProvider theme={theme}>
        {inputs.map((elem, index) => (
          <Grid
            key={index}
            item
            xs={12}
            sm={elem.type === "multiline" ? 12 : 6}
          >
            <TextField
              label={elem.label}
              multiline={elem.type === "multiline" ? true : false}
              rows={elem.row}
              helperText={elem.helper}
              className={elem.width === "90" ? classes.txtf2 : classes.txtf}
              placeholder={elem.placeholder ? elem.placeholder : ""}
              color="secondary"
            />
          </Grid>
        ))}
      </MuiThemeProvider>
      <Grid container item justify="flex-end">
        <Box alignItems="flex-end" display="flex">
          <ColorButton variant="contained" color="primary">
            Send Message
          </ColorButton>
        </Box>
      </Grid>
    </Grid>
  );
};
