import React from "react";
import PropTypes from "prop-types";
import SwipeableViews from "react-swipeable-views";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import PersonRoundedIcon from "@material-ui/icons/PersonRounded";
import VpnKeyRoundedIcon from "@material-ui/icons/VpnKeyRounded";
import {GeneralSettings , ChangePassword} from "./AccountTabs"
function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={index}
      aria-labelledby={index}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          {children}
        </Box>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: index,
    "aria-controls": index,
  };
}
const useStyles = makeStyles((theme) => ({
  root: {
    // backgroundColor: theme.palette.background.paper,
    // width: 500,
  },
  wrapper: {
    display: "flex",
    flexDirection: "row",
  },
  typo: {
    fontFamily: "Public Sans, sans-serif",
    fontWeight: "bold",
    fontSize: "0.8rem",
    textTransform: "capitalize",
    marginLeft: theme.spacing(1),
    [theme.breakpoints.down("sm")]: {
      display: "none",
    },
  },
}));

function typo(text, classes) {
  return (
    <Typography className={classes.typo} variant="caption">
      {text}
    </Typography>
  );
}

export default function AccountSettings(props) {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
    setValue(index);
  };
  const matches = useMediaQuery(theme.breakpoints.down("sm"));
  return (
    <div className={classes.root}>
      <Tabs
        value={value}
        onChange={handleChange}
        indicatorColor="primary"
        textColor="primary"
        variant={!matches ? "standard" : "fullWidth"}
      >
        <Tab
          label={typo("Account Settings", classes)}
          icon={<PersonRoundedIcon style={{ fontSize: "1.3rem" }} />}
          {...a11yProps(0)}
          classes={{ wrapper: classes.wrapper }}
        />
        <Tab
          label={typo("Change Password", classes)}
          icon={<VpnKeyRoundedIcon style={{ fontSize: "1.3rem" }} />}
          {...a11yProps(1)}
          classes={{ wrapper: classes.wrapper }}
        />
      </Tabs>
      <SwipeableViews
        axis={theme.direction === "rtl" ? "x-reverse" : "x"}
        index={value}
        onChangeIndex={handleChangeIndex}
      >
        <TabPanel value={value} index={0} dir={theme.direction}>
          <GeneralSettings {...props}/>
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
        <ChangePassword/>
        </TabPanel>
      </SwipeableViews>
    </div>
  );
}
